import * as React from 'react';
import { toast } from 'react-toastify';
import { Typography } from '@material-tailwind/react';
import services from '../../../../../utils/services';

export default function Education({ activeBar, tabId }) {
  const [formData, setFormData] = React.useState({
    isSubmit: false,
    fields: []
  });
  const [isLoading, setIsLoading] = React.useState(false);
  const errorNotify = (content) => toast.error(content);

  React.useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const forms = await services.get('/master/form-application/' + tabId);
        setFormData((prevData) => ({ ...prevData, fields: forms.data }));
        setIsLoading(false);
      } catch (error) {
        errorNotify(error.response.statusText);
        setIsLoading(false)
      };
    };

    if (activeBar === 3) fetchData();
  }, [activeBar]);

  console.log(formData.fields);

  if (isLoading) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };
  return (
    <React.Fragment>
      <Typography variant="lead" className="font-bold text-black">Education</Typography>
      <hr className="my-5" />
    </React.Fragment >
  );
};