import * as React from 'react';
import { toast } from 'react-toastify';
import { Card, List, ListItem, ListItemPrefix, Typography } from '@material-tailwind/react';
import BasicInformation from './Content/BasicInformation';
import Address from './Content/Address';
import Education from './Content/Education';
import WorkExperience from './Content/WorkExperience';
import OrganizationalExperience from './Content/OrganizationalExperience';
import TrainingCertification from './Content/TrainingCertification';
import Skill from './Content/Skill';
import Achievement from './Content/Achievement';
import SocialMedia from './Content/SocialMedia';
import Family from './Content/Family';
import FamilyBI from './Content/FamilyBI';
import SupportingDocument from './Content/SupportingDocument';
import UserIcon from '../assets/icons/User';
import MapIcon from '../assets/icons/Map';
import MortarboardIcon from '../assets/icons/Mortarboard';
import BriefcaseIcon from '../assets/icons/Briefcase';
import TeamworkIcon from '../assets/icons/Teamwork';
import AnalysisIcon from '../assets/icons/Analysis';
import SkillIcon from '../assets/icons/Skill';
import AwardIcon from '../assets/icons/Award';
import SocialMediaIcon from '../assets/icons/SocialMedia';
import FamilyIcon from '../assets/icons/Family';
import GoogleDocsIcon from '../assets/icons/GoogleDocs';
import services from '../../../../utils/services';
import sessions from '../../../../utils/sessions';

export default function FormContent() {
  const userSession = JSON.parse(sessions.getSessionUser() || '{}');
  const [activeBar, setActiveBar] = React.useState(0);
  const [tabs, setTabs] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(false);
  const errorNotify = (content) => toast.error(content);

  React.useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const result = await services.get('/master/tab-form');
        setTabs(result.data);
        setIsLoading(false);
        setActiveBar(1);
      } catch (error) {
        errorNotify(error.response.statusText);
        setIsLoading(false);
      };
    };
    fetchData();
  }, []);

  const icons = (icon) => {
    switch (icon) {
      case 'UserIcon':
        return (<UserIcon />)
      case 'MapIcon':
        return (<MapIcon />)
      case 'MortarboardIcon':
        return (<MortarboardIcon />)
      case 'BriefcaseIcon':
        return (<BriefcaseIcon />)
      case 'TeamworkIcon':
        return (<TeamworkIcon />)
      case 'AnalysisIcon':
        return (<AnalysisIcon />)
      case 'SkillIcon':
        return (<SkillIcon />)
      case 'AwardIcon':
        return (<AwardIcon />)
      case 'SocialMediaIcon':
        return (<SocialMediaIcon />)
      case 'FamilyIcon':
        return (<FamilyIcon />)
      case 'GoogleDocsIcon':
        return (<GoogleDocsIcon />)
      default:
        break;
    };
  };

  const forms = React.useCallback(() => {
    switch (activeBar) {
      case 1:
        return (<BasicInformation activeBar={activeBar} tabId={tabs[0].id} />);
      case 2:
        return (<Address activeBar={activeBar} tabId={tabs[1].id} />);
      case 3:
        return (<Education activeBar={activeBar} tabId={tabs[2].id} />);
      case 4:
        return (<WorkExperience tabId={tabs[(activeBar-1)].id}/>);
      case 5:
        return (<OrganizationalExperience tabId={tabs[(activeBar-1)].id}/>);
      case 6:
        return (<TrainingCertification tabId={tabs[(activeBar-1)].id}/>);
      case 7:
        return (<Skill />);
      case 8:
        return (<Achievement />);
      case 9:
        return (<SocialMedia tabId={tabs[(activeBar-1)].id}/>);
      case 10:
        return (<Family />);
      case 11:
        return (<FamilyBI />);
      case 12:
        return (<SupportingDocument activeBar={activeBar} tabId={tabs[11].id} />);
      default:
        break;
    };
  }, [activeBar]);

  if (isLoading) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };

  return (
    <div className="flex mt-2">
      <div className="h-screen w-full max-w-[20rem] p-4 border rounded-l-lg">
        <List>
          {tabs.map((item, idx) =>
            <React.Fragment key={idx}>
              <ListItem onClick={() => setActiveBar(idx + 1)} selected={activeBar === idx + 1}
                style={{ backgroundColor: activeBar === idx + 1 ? '#EFF2FF' : 'transparent' }}>
                <ListItemPrefix>
                  {icons(item.icon)}
                </ListItemPrefix>
                {item.name}
              </ListItem>
              {idx !== tabs.length - 1 ?
                <hr /> :
                null}
            </React.Fragment>
          )}
        </List>
      </div>
      {![4, 5, 6, 9].includes(activeBar) ? (
        [8, 10, 11].includes(activeBar) ? (
          <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border-none bg-transparent">
            {forms()}
          </Card>
        ) : (
          <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border p-6">
            {forms()}
          </Card>
        )
      ) : (
        <div className="w-full overflow-hidden flex flex-col gap-5">
          {forms()}
        </div>
      )}
    </div>
  );
};